
import Toast from 'vux/src/components/toast/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Confirm from 'vux/src/components/confirm/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

export default {
  directives: {
    TransferDom
  },
  components: {
    XButton,    
    PopupPicker,
    Confirm,
    TransferDom,
    Toast,
    Grid,
    GridItem,
    Datetime
  },
  data () {
      return {
        show: false,
        showPositionValue: false,
        tips:"",
        items:[],
        interviewDate:new Date(),
        jobname:this.$route.params.jobname,
        idx:0,//当前选择索引,
        moreDate:'更多',
        headerDate:[],
        headerIdx:0
      }
  },
  mounted() {
    this.getDescribeTime(this.addDate(this.interviewDate,1))// 获取预约时间段
    this.createDate()//创建头部日期选项
  },
  filters:{
    formatTime(value){
      var dateee = new Date(value).toJSON(); 
      var _obj=new Date(dateee).toISOString().replace(/T/g,' ').replace(/\.[\d]{3}Z/,'')// +8*3600*1000
      _obj=new Date(_obj)
      var _h=_obj.getHours();
      if(_h.toString().length==1) _h="0"+_h
      var _m=_obj.getMinutes();
      if(_m.toString().length==1) _m="0"+_m
      return _h+" : "+_m;
    }
  },
  methods:{
    change(value){
      if(value){
        this.moreDate=''
        this.headerIdx=4
        this.getDescribeTime(value)
      }
      console.log(value)
    },
    onCancel () {
      console.log('on cancel')
    },
    onConfirm () {
      this.showPositionValue = true
        this.$http({
          method:'POST',
          url:this.$store.state.domain+'api/XiechengJobAPI/SetApplyInterviewTime',
          body:{"applyId": this.$route.params.id,"interviewTime": this.items[this.idx].time},
          headers: {"Authorization": this.$store.state.jwt.token}
          })
      .then((d) => {
              if(d.data.success){
                this.showPosition('预约成功');
                this.items[this.idx].isUsed=true;
              }else{
                this.showPosition(d.data.message);
              }
            }, (d) => {
              alert('网络繁忙！请稍候重再试');
      });
    },
    onHide () {
    },
    onShow () {
    },
    showPosition (t) {
      this.showPositionValue = true
      this.tips=t;
    },
    describe(i) {
      this.show=true;
      this.idx=i;
    },
    addDate(date, days) {
            if (days == undefined || days == '') {
                days = 1;
            }
            var date = new Date(date);
            date.setDate(date.getDate() + days);
            var month = date.getMonth() + 1;
            var day = date.getDate();
            return date.getFullYear() + '-' + this.getFormatDate(month) + '-' + this.getFormatDate(day);
     },
     getFormatDate(arg) {
            if (arg == undefined || arg == '') {
                return '';
            }

            var re = arg + '';
            if (re.length < 2) {
                re = '0' + re;
            }

            return re;
      },
    createDate(){
      var arr_week = new Array("星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六");
      var dateee = this.interviewDate.toJSON(); 
      var _obj=new Date(dateee).toISOString().replace(/T/g,' ').replace(/\.[\d]{3}Z/,'')// +8*3600*1000

      for(var i=1;i<5;i++){
        var _time=this.addDate(_obj,1);
        _obj=new Date(_time)
          var _m=_obj.getMonth()+1;
          var _date=_obj.getDate();
          var _day=arr_week[_obj.getDay()];
          var _subobj={
            date:_m+"/"+_date,
            day:_day,
            time:_time
          }
         this.headerDate.push(_subobj)
      }
    },
    changeDate(index){
      this.getDescribeTime(this.headerDate[index].time)
      this.headerIdx=index;
    },
    getDescribeTime(t){
      this.$http({
          method:'POST',
          url:this.$store.state.domain+'api/XiechengJobAPI/GetInterviewTimeStatus',
          body:{"applyId": this.$route.params.id,"interviewDate": t},
          headers: {"Authorization": this.$store.state.jwt.token}
          })
      .then((d) => {
              if(d.data.success){
                this.items=d.data.content.times
              }
            }, (d) => {
              alert('网络繁忙！请稍候重再试');
      });
    }
  }
}



